export default {
  name: 'FadeInLeft',
  props: {
    duration: {
      type: String,
      default: '1s',
    },
    delay: {
      type: String,
      default: '0s',
    },
  },
  data() {
    return {
      startAnimate: false,
    };
  },
  mounted() {
    requestAnimationFrame(() => {
      this.startAnimate = true;
    });
  },
};
